body{margin:0;font-family:"'Work Sans', sans-serif";-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#fff}code{font-family:"'Work Sans', sans-serif"}.preloader{position:fixed;top:0;left:0;width:100%;height:100%;z-index:99999;display:flex;flex-flow:row nowrap;justify-content:center;align-items:center;background:none repeat scroll 0 0 #ffffff}.sk-spinner-wordpress.sk-spinner{background-color:#749370;width:40px;height:40px;border-radius:40px;position:relative;-webkit-animation:sk-innerCircle 1s linear infinite;animation:sk-innerCircle 1s linear infinite}.sk-spinner-wordpress .sk-inner-circle{display:block;background-color:#ffffff;width:16px;height:16px;position:absolute;border-radius:8px;top:5px;left:5px}@-webkit-keyframes sk-innerCircle{0%{transform:rotate(0)}100%{transform:rotate(360deg)}}@keyframes sk-innerCircle{0%{transform:rotate(0)}100%{transform:rotate(360deg)}}

